@tailwind base;
@tailwind components;
@tailwind utilities;


@font-face {
  font-family: "ArchivoBlack";
  src: url("./assets/fonts/ArchivoBlack-Regular.ttf");
}

* {
  box-sizing: border-box;
}

body {
  @apply font-sans bg-black;
}

h1 {
  @apply text-4xl sm:text-7xl font-extrabold font-ArchivoBlack;
}
h2 {
  @apply text-xl sm:text-5xl  font-sans;
}
h3 {
  @apply font-extrabold text-base sm:text-2xl font-ArchivoBlack;
}

form input {
  @apply px-3 py-1 border border-orange-400 ;
}
form input:focus {
  @apply border-red-400 text-black;
}
form input[type='submit'] {
  @apply bg-orange-600 text-white border-0 capitalize;
}


.bg-me {
  background-image: url('./assets/images/jcole.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.icon {
  width: 13px;
  height: 13px;
}


.workShowBox {
  position: relative;
  overflow: hidden;
}
.workShowBox:hover {
  /* overflow: hidden; */
}
.workShowBox:hover .descr {
  /* display: none; */
  top: -100%;
}
.workShowBox .descr {
  display: inherit;
  padding: 15px;
  transition: top .3s;
}


.test {
  border: 1px solid red;
}